<template>
  <li>
    <a :href="link" :target="target">{{ linkDesc }}</a
    ><b v-if="subitems.length > 0">></b>
  </li>
</template>

<script>
export default {
  props: {
    link: {
      type: String,
      required: true,
    },
    linkDesc: {
      type: String,
      required: true,
    },
    target: {
      type: String,
      required: false,
      default: '_self',
    },
    subitems: {
      type: Array,
      default: () => [],
    },
  },
  emits: ['close'],
};
</script>

<style scoped>
li {
  padding: 3px;
  margin: 5px;
  list-style-type: none;
}
a {
  padding: 20px;
}
b {
  padding-left: 0px;
}
</style>
